@charset "UTF-8";

// NHA-COMMON-ATM_LIST-020
.item_list_type03 {
    article {
        &.financial_type {padding:70px 0 0;}
        h3 {font-size:30px; font-family:$bebasNeue; font-weight:$regular; padding:0 0 20px;}
        .type_wrap {display:flex;width:100%;flex-wrap:wrap;}
        .item_elm {
            width:24.166%;border:1px solid $gray2;margin:1.083% 0 0 1.083%; padding:30px 30px 20px;
            &:first-of-type, &:nth-of-type(4n+5) { margin:1.083% 0 0;}
            @include boxSizing;
            @include borderRadius(20px);
            strong {display:block;font-size:23px;color:$gray7;}
            p {
                min-height:56px; padding:15px 0 0;font-size:17px;line-height:28px; font-weight:$regular;color:$gray5;
                overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;
            }
        }
        .img_item {
            width:100%; max-width:200px; display:flex;padding:20px 0 0;margin:0 auto;
            @include borderRadius(10px);
            img { width:100%; }
        }
    }
}

// NHA-COMMON-Managed_Services_List-077
.item_list_type04 {
    display:flex;width:100%;flex-wrap:wrap;
    &.financial_type {padding:70px 0 0;}
    h3 {font-size:30px; font-family:$bebasNeue; font-weight:$regular; padding:0 0 20px;}
    .item_elm {
        min-height:400px; width:24.166%;border:1px solid $gray2;margin:60px 0 0 1.083%; padding:30px;
        display:flex;flex-direction:column;justify-content:space-between;
        &:nth-of-type(-n+4) {margin:0 0 0 1.083%;}
        &:first-of-type { margin:0; }
        &:nth-of-type(4n+5) { margin:60px 0 0;}
        @include boxSizing;
        @include borderRadius(20px);
        strong {display:block;font-size:23px;color:$gray7; line-height:33px;}
    }
    .key_txt {
        p {padding:15px 0 0;font-size:17px;line-height:28px; font-weight:$regular;color:$gray5;}
        .desc {
            font-size:17px;line-height:25px;font-weight:$regular;color:$gray7;padding:20px 0 0;
            border-top:1px solid $gray2; margin:20px 0 0;
        }
    }
    .form_text_type01 {
        input {
            width:105px;
            background-color:$gray1;
            border-color:$gray1;
        }
    }
    .basket_form_elem {
        padding:20px 0 0;display:flex;justify-content:space-between;align-items:center;
        .wish_wrap {display:flex;}
        button {
            width:50px; height:50px;text-indent:-99999px; position:relative;
            @include transition(background, .2s);
            &:hover, &:focus {background-color:#f5f5f5;}
        }
        .wish_btn {background:url(./../../images/common/wish_icon.png) 50% 50% no-repeat;}
        .cart_btn {background:url(./../../images/common/cart_icon.png) 50% 50% no-repeat;}
    }
}





































